*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-family:"Roboto",Arial,sans-serif;font-size:1.6rem;color:#fff;background:#0a1621;padding-top:90px}.nav{display:flex;align-items:center;height:8rem;background:#0a1621;padding:2rem 5rem;letter-spacing:0.12em;position:fixed;top:0;left:0;z-index:1000;width:100%;box-shadow:0 0 5px #5275a8}.nav__title-text{font-size:3rem}.nav__bar{display:flex;list-style:none;right:0;position:absolute;margin-right:0.5em}.nav__bar-item{cursor:pointer;display:block;padding:0.8rem 1.6rem;position:relative;background:transparent;border-radius:5px;border:2px solid #fc7a1e;margin-right:2rem}.nav__bar-item::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;background:#fc7a1e;transform:scaleX(0);transform-origin:left;transition:transform 0.2s ease}.nav__bar-item:hover::after,.nav__bar-item:focus::after{transform:scaleX(1)}.nav__bar-item--active::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;background:#fc7a1e;transform:scaleX(1);transform-origin:left;transition:transform 0.2s ease}.nav__list{font-size:1.6rem;position:absolute;padding:20px 50px;right:0;top:100%;max-width:100%;list-style:none;opacity:0;transition:opacity 0.3s ease-in-out, transform 0.2s 0.1s ease-in-out;transform:scaleY(0);transform-origin:top}.nav__list--active{opacity:1;transition:opacity 0.3s 0.2s ease-in-out, transform 0.3s 0.1s ease-in-out;transform:scaleY(1)}.nav__list-item{font-size:3em;letter-spacing:0.12em;cursor:pointer;display:block;padding:0.5em;position:relative;background:transparent;color:#fff;border:none}.nav__list-item::after{content:"";position:absolute;left:-20px;z-index:-1;transition:opacity 0.1s linear;-webkit-filter:drop-shadow(2px 2px 5px rgba(237,159,75,0.4));filter:drop-shadow(2px 2px 5px rgba(237,159,75,0.4));transform:rotate(35deg);opacity:0}.nav__list-item:hover{color:#fc7a1e}.nav__list-item:hover::after{opacity:1}.nav__list-item--active{color:#5275a8}.burger{height:3em;width:3em;position:relative;font-size:12px;cursor:pointer;transition:0.2s all;-webkit-tap-highlight-color:transparent}.burger__wrapper{display:none;position:fixed;right:0;margin-right:2em}.burger .burger-lines:after{left:0;top:-1em}.burger .burger-lines:before{left:1em;top:1em}.burger:after{content:"";display:block;position:absolute;height:150%;width:150%;top:-25%;left:-25%}.burger .burger-lines{top:50%;margin-top:-0.125em}.burger .burger-lines,.burger .burger-lines:after,.burger .burger-lines:before{pointer-events:none;display:block;content:"";width:100%;border-radius:0.25em;background-color:#fff;height:0.25em;position:absolute;transform:rotate(0)}.burger .burger-lines:after{left:0;top:-1em}.burger .burger-lines:before{left:1em;top:1em}.burger.burger-arrow .burger-lines,.burger.burger-arrow .burger-lines:after,.burger.burger-arrow .burger-lines:before{transition:0.2s top, 0.2s left, 0.2s transform, 0.4s background-color 0.2s}.burger.burger-arrow .burger-lines:after,.burger.burger-arrow .burger-lines:before{width:2em}.burger.burger-arrow.burger-right{transform:rotateY(180deg)}.burger.burger-arrow.burger-right .burger-lines:after{left:1em;top:-1em}.burger.burger-arrow.burger-right .burger-lines:before{left:0em;top:1em}.burger.burger-arrow.open .burger-lines,.burger.burger-arrow.open .burger-lines:after,.burger.burger-arrow.open .burger-lines:before{transition:0.2s background-color, 0.2s top, 0.2s left, 0.2s transform}.burger.burger-arrow.open .burger-lines{background-color:#fff}.burger.burger-arrow.open .burger-lines:before,.burger.burger-arrow.open .burger-lines:after{left:-0.3em;top:0px}.burger.burger-arrow.open .burger-lines:before{top:-0.62em;transform:rotate(-45deg)}.burger.burger-arrow.open .burger-lines:after{top:0.62em;transform:rotate(45deg)}@media (max-width: 750px){.nav__bar{display:none}.burger__wrapper{display:block}}@media (max-width: 450px){.nav{padding:1rem}}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-family:"Roboto",Arial,sans-serif;font-size:1.6rem;color:#fff;background:#0a1621;padding-top:90px}.footer{width:100%;height:90px;display:flex;padding:15px;justify-content:center}.footer__link{border:solid 2px #fff;border-radius:50%;position:relative;top:0;left:0;height:4.9rem;width:4.9rem;margin-right:2rem;padding:0.3rem;display:inline-block;cursor:pointer;opacity:0.9}.footer__link::after{content:"";border-radius:50%;position:absolute;top:0;left:0;height:102%;width:102%;z-index:-1;background:#fff;transform:scaleX(0)}.footer__link:hover::after,.footer__link:focus::after{transform:scaleX(1)}.footer__link-svg{fill:#fff}.footer__link-svg:hover path{fill:#0a1621}#linkedin{height:3rem;margin-left:0.5rem;margin-top:0.2rem}#envelope{height:2.4rem;margin-left:0.4rem;margin-top:0.7rem}#pdf{height:3.3rem;margin-left:0.8rem;margin-top:0.25rem}

