*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-family:"Roboto",Arial,sans-serif;font-size:1.6rem;color:#fff;background:#0a1621;padding-top:90px}.loading{width:100vw;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.loading__spinner{display:inline-block;position:relative;width:80px;height:80px}.loading__spinner div{animation:loading__spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;transform-origin:40px 40px}.loading__spinner div:after{content:" ";display:block;position:absolute;width:14px;height:14px;border-radius:50%;background:#fc7a1e;margin:-4px 0 0 -4px}.loading__spinner div:nth-child(1){animation-delay:-0.036s}.loading__spinner div:nth-child(1):after{top:63px;left:63px}.loading__spinner div:nth-child(2){animation-delay:-0.072s}.loading__spinner div:nth-child(2):after{top:68px;left:56px}.loading__spinner div:nth-child(3){animation-delay:-0.108s}.loading__spinner div:nth-child(3):after{top:71px;left:48px}.loading__spinner div:nth-child(4){animation-delay:-0.144s}.loading__spinner div:nth-child(4):after{top:72px;left:40px}.loading__spinner div:nth-child(5){animation-delay:-0.18s}.loading__spinner div:nth-child(5):after{top:71px;left:32px}.loading__spinner div:nth-child(6){animation-delay:-0.216s}.loading__spinner div:nth-child(6):after{top:68px;left:24px}.loading__spinner div:nth-child(7){animation-delay:-0.252s}.loading__spinner div:nth-child(7):after{top:63px;left:17px}.loading__spinner div:nth-child(8){animation-delay:-0.288s}.loading__spinner div:nth-child(8):after{top:56px;left:12px}.loading__title{text-transform:uppercase;margin-top:4rem;font-size:4rem;letter-spacing:0.12em;font-weight:bold}@keyframes loading__spinner{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.content{position:relative;transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform-origin:left}.content.moved{transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform:translateX(-50%);opacity:0.5}@media (max-width: 450px){.content.moved{transform:translateX(-90%)}}

